const ajax =
  new XMLHttpRequest(); /* Î∏åÎùºÏö∞Ï†ÄÍ∞Ä ÎπÑÎèôÍ∏∞ Ï≤òÎ¶¨Î•º ÏúÑÌï¥ Ï†úÍ≥µÌïòÎäî ÎÇ¥Ïû•Í∞ùÏ≤¥ */
const TITLE_URL = "https://api.hnpwa.com/v0/news/1.json";//Î™©Î°ù
const container = document.getElementById("root");
const content = document.createElement("div");
/* Î™©Î°ù Î≥¥Í∏∞ ÎàåÎ†ÄÏùÑÎïå Ï≤òÏùåÌéòÏù¥ÏßÄÍ∞Ä ÏïÑÎãàÎùº Í∏Ä Ï†úÎ™© ÎàÑÎ•∏ ÌéòÏù¥ÏßÄÎ°ú ÎèåÏïÑÏôÄÏïºÌïúÎã§
Ïñ¥Ï†úÍπåÏßÄ #38099086 - substring(1) -> 38099086
http://localhost:1234/#38099086

Ïò§ÎäòÎ∂ÄÌÑ∞
ÌéòÏù¥ÏßÄ Î≤àÌò∏Í∞Ä Í≥ÑÏÜç Î∞îÎÄêÎã§ - ÏÉÅÌÉú - (ÏÑ∏ÏÖò Ïø†ÌÇ§ - Î¶¨ÎçïÏä§)
ÏÉÅÏÑ∏ÌéòÏù¥ÏßÄ - Íµ¨Î∂Ñ - show
Î™©Î°ùÌéòÏù¥ÏßÄ - Íµ¨Î∂Ñ - page
http://localhost:1234/#/page/1
http://localhost:1234/#/page/2
http://localhost:1234/#/show/2
http://localhost:1234/#/show/2
*/
const CONTENT_URL = "https://api.hnpwa.com/v0/item/@id.json";

//ÌéòÏù¥Ïßï Ï≤òÎ¶¨Î•º ÏúÑÌïú ÌïÑÏöîÌïú ÏÉÅÌÉúÍ∞íÏùÑ Í¥ÄÎ¶¨ÌïòÎäî Í∞ùÏ≤¥ (VOÍ∞ôÏùÄ ÎäêÎÇå)
const store = {
  currentPage : 1,
  totalRecord : 47,
}

//ÏóêÎ°úÏö∞ function Î∞©Ïãù
getData = (url) => {
  //arrow function ÏúºÎ°ú Ï≤òÎ¶¨ - ES6Ï∂îÍ∞ÄÎêú Ïª®Î≤§ÏÖò
  ajax.open("GET", url, false);
  ajax.send();
  return JSON.parse(ajax.response);
};

//Í∏Ä ÎÇ¥Ïö©ÏùÑ Î≥¥Î†§Î©¥ Í∏ÄÎ™©Î°ù ÌôîÎ©¥ÏóêÏÑú Ï†úÎ™©ÏùÑ ÌÅ¥Î¶≠ÌñàÏùÑ Îïå (aÌÉúÍ∑∏Í∞Ä Î∞úÎèôÎêòÏóàÏùÑ Îïå) ÏÉÅÏÑ∏ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
getNewsData = () => {
  const newsData = getData(TITLE_URL);
  const newsList = [];
  newsList.push("<ul>");
  for (let i = (store.currentPage -1)*10; i < store.currentPage*10; i++) {
    //<a> - ÏÉÅÏÑ∏Î≥¥Í∏∞ (Í∏Ä ÎÇ¥Ïö©Î≥¥Í∏∞Î°ú Ïù¥Îèô) - #/show/
    // localhost:1234/#/show/2      xxx.substring(7)-Number('2') = Interger.parseInt() 
    newsList.push(`
      <li>
        <a href = "#/show/${newsData[i].id}">
          ${newsData[i].title}( click ${newsData[i].comments_count} )
        </a>
      </li>
    `);
  }
  newsList.push("</ul>");

  //ÌéòÏù¥Ïßï Ï≤òÎ¶¨
  newsList.push(`
    <div>
      <a href = "#/page/${store.currentPage > 1 ? store.currentPage -1 : 1}">Ïù¥Ï†ÑÌéòÏù¥ÏßÄ</a>
      <a href = "#/page/${store.currentPage < 3 ? store.currentPage +1 : 3}">Îã§ÏùåÌéòÏù¥ÏßÄ</a>
    </div>
  `)

  container.innerHTML = newsList.join("");
}; //end of getNewsData

newsDetail = () => {
  const id = location.hash.substring(7);
  const ncontent = getData(CONTENT_URL.replace("@id", id));
  container.innerHTML = `
  <h1>${ncontent.title}</h1>
  <div><a href="#/page/${store.currentPage}">Î™©Î°ùÏúºÎ°ú</a></div>
  `;
}; //end of newsDetail

router = () => {
  const routerPath = location.hash;
  console.log(routerPath);
  if (routerPath === "") {
    // === : ÌÉÄÏûÖÍπåÏßÄ ÎπÑÍµê
    getNewsData(); //Í∏ÄÎ™©Î°ù Î≥¥Í∏∞
  
  }else if(routerPath.indexOf('#/page/') >= 0){  
    // -> #/page/ Ïù¥Îü∞ ÌòïÌÉúÎ°ú Îì§Ïñ¥ÏôÄ ÏûàÏúºÎ©¥ Ïù¥Í≤å 0 Î≥¥Îã§ ÌÅ¨Î©¥ pageÎùºÍ≥† ÌïòÎäî Ìï¥ÏãúÎ≥ÄÍ≤Ω ÌôïÏù∏
    /* currentPageÏóê 2ÌéòÏù¥ÏßÄ, 3ÌéòÏù¥ÏßÄ Ïù¥Î†áÍ≤å Îì§Ïñ¥Í∞Ä ÏûàÏùå ÌéòÏù¥ÏßÄ Îí§Ïóê Ïà´ÏûêÍ∞íÏùÑ ÎÑ£ÏúºÎ©¥ Îê®
    Í∑∏Îü∞Îç∞ Ïö∞Î¶¨Îäî ÏïÑÏßÅ Ï∂îÏ∂úÌïòÏßÄ ÏïäÏïòÏúºÎãà ÏÉÅÏàòÎ°ú 2Î•º Ï£ºÏñ¥ ÎèôÏûë ÌôïÏù∏  */
    store.currentPage = Number(routerPath.substring(7)); //ÌòÑÏû¨ ÌéòÏù¥ÏßÄÍ∞í
    getNewsData();// Í∏ÄÎÇ¥Ïö© Î≥¥Í∏∞
  }else {
    newsDetail(); //Í∏Ä ÎÇ¥Ïö© Î≥¥Í∏∞
  }
};

addEventListener("load", () => {});

addEventListener("hashchange", router);
router(); //Ï†ÑÏ≤òÎ¶¨Í≥ºÏ†ï
